<template>
  <div class="login">
    <div class="login-box">
      <img src="../assets/login.png" alt="" class="login-title">
      <div class="input-suffix">
        <label class="input-label">用户：</label>
        <el-input
          placeholder="请输入用户名称"
          prefix-icon="el-icon-user"
          type="text"
          maxlength="10"
          show-word-limit
          v-model="user">
        </el-input>
      </div>
      <div class="input-suffix">
        <label class="input-label">密码：</label>
        <el-input
          placeholder="请输入密码"
          prefix-icon="el-icon-lock"
          type="password"
          maxlength="16"
          minlength="6"
          v-model="passwd">
        </el-input>
      </div>
      <div class="input-suffix">
        <label class="input-label">验证：</label>
        <VueSimpleVerify ref="verify" :width="360" :height="40" @success="success"/>
      </div>
      <div class="btn-group">
        <el-button type="primary" @click="login">登录</el-button>
        <el-button @click="register">注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import md5 from 'js-md5'
import VueSimpleVerify from 'vue-simple-verify'
import '../../node_modules/vue-simple-verify/dist/vue-simple-verify.css'

export default {
  name: 'Login',
  components: {
    VueSimpleVerify
  },
  data () {
    return {
      user: '',
      passwd: '',
      verify: false
    }
  },
  created () {
    sessionStorage.removeItem('isLogin')
    this.setUser('')
    this.enter()
  },
  methods: {
    ...mapMutations(['setUser']),
    success () {
      this.verify = true
    },
    login () {
      const userInfo = JSON.parse(localStorage.getItem('userInfo')) || {
        user: 'test',
        passwd: 'e10adc3949ba59abbe56e057f20f883e'
      }
      if (this.user !== userInfo.user) return this.$message.error('用户名错误')
      if (md5(this.passwd) !== userInfo.passwd) return this.$message.error('密码错误')
      if (!this.verify) return this.$message.error('请先完成验证')
      this.setUser(this.user)
      sessionStorage.setItem('isLogin', true)
      this.$message({
        message: '欢迎您，' + this.user,
        type: 'success',
        duration: 1500,
        onClose: () => {
          this.$router.push('/')
        }
      })
    },
    enter () {
      document.onkeydown = (e) => {
        const key = window.event.keyCode
        if (key === 13) {
          this.login()
        }
      }
    },
    register () {
      this.$router.push('/register')
    }
  }
}
</script>

<style scoped>
.login {
  width: 100%;
  height: 100vh;
  background-color: #c9c9c7;
  background-image: url("~@/assets/login-bg.png");
  background-repeat: no-repeat;
  background-position: left center;
  overflow: hidden;
}

.login-box {
  width: 360px;
  padding: 40px;
  background: rgba(0, 0, 0, .5);
  border-radius: 5px;
  position: fixed;
  top: 50%;
  left: 50%;
  /*transform: translateX(-50%) translateY(-50%);*/
  transform: translateY(-50%);
}

.login-title {
  display: block;
  width: 135px;
  margin: auto;
  margin-bottom: 20px;
}

.input-suffix {
  margin-bottom: 20px;
}

.input-label {
  display: block;
  font-size: 16px;
  color: #ffffff;
  margin-bottom: 10px;
}

.btn-group {
  text-align: right;
}
</style>
