<template>
  <div class="register">
    <div class="register-box">
      <img src="../assets/register.png" alt="" class="register-title">
      <div class="input-suffix">
        <label class="input-label">设置用户名：</label>
        <el-input
          placeholder="请输入用户名称"
          prefix-icon="el-icon-user"
          type="text"
          maxlength="10"
          show-word-limit
          v-model="user">
        </el-input>
      </div>
      <div class="input-suffix">
        <label class="input-label">设置密码：</label>
        <el-input
          placeholder="请输入密码"
          prefix-icon="el-icon-lock"
          type="password"
          maxlength="16"
          minlength="6"
          v-model="passwd">
        </el-input>
      </div>
      <div class="input-suffix">
        <label class="input-label">设置邮箱：</label>
        <el-input
          placeholder="请输入邮箱"
          prefix-icon="el-icon-message"
          type="email"
          v-model="email">
        </el-input>
      </div>
      <div class="btn-group">
        <el-button type="primary" @click="save">提交</el-button>
        <el-button @click="back">返回</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import md5 from 'js-md5'

export default {
  name: 'Register',
  data () {
    return {
      user: '',
      passwd: '',
      email: ''
    }
  },
  created () {
    this.enter()
  },
  methods: {
    save () {
      if (this.user === '') return this.$message.error('请输入用户名称')
      if (this.passwd === '') return this.$message.error('请设置密码')
      if (this.email === '') return this.$message.error('请设置邮箱')
      const userData = {
        user: this.user,
        passwd: md5(this.passwd),
        email: this.email
      }
      localStorage.setItem('userInfo', JSON.stringify(userData))
      this.$message({
        message: '注册成功，即将前往登录',
        type: 'success',
        duration: 1500,
        onClose: () => {
          this.$router.push('/login')
        }
      })
    },
    enter () {
      document.onkeydown = (e) => {
        const key = window.event.keyCode
        if (key === 13) {
          this.save()
        }
      }
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.register {
  width: 100%;
  height: 100vh;
  background-color: #c9c9c7;
  background-image: url("~@/assets/login-bg.png");
  background-repeat: no-repeat;
  background-position: left center;
  overflow: hidden;
}

.register-box {
  width: 360px;
  padding: 40px;
  background: rgba(0, 0, 0, .5);
  border-radius: 5px;
  position: fixed;
  top: 50%;
  left: 50%;
  /*transform: translateX(-50%) translateY(-50%);*/
  transform: translateY(-50%);
}

.register-title {
  display: block;
  width: 135px;
  margin: auto auto 20px;
}

.input-suffix {
  margin-bottom: 20px;
}

.input-label {
  display: block;
  font-size: 16px;
  color: #ffffff;
  margin-bottom: 10px;
}

.btn-group {
  text-align: right;
}
</style>
