<template>
  <div class="head">
    <el-page-header @back="goBack" :content="currentTitle">
    </el-page-header>
  </div>
</template>

<script>
import router from '@/router'
import { mapState } from 'vuex'

export default {
  name: 'Head',
  data () {
    return {}
  },
  computed: {
    ...mapState({
      currentTitle: state => state.title
    })
  },
  created () {
  },
  methods: {
    goBack () {
      router.back()
    }
  }
}
</script>

